@tailwind base;
@tailwind components;
@tailwind utilities;

/* Custom animations and styles */
@import url('https://fonts.googleapis.com/css2?family=Lateef&display=swap');

@keyframes fade-in {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}
.animate-fade-in {
  animation: fade-in 1s ease-out forwards;
}
.hover-lift {
  transition: transform 0.3s ease-out;
}
.hover-lift:hover {
  transform: translateY(-5px);
}
.text-glow {
  text-shadow: 0 0 15px rgba(245,158,11,0.5);
}
.card-glow {
  box-shadow: 0 0 30px rgba(245,158,11,0.1);
}
.gradient-border {
  position: relative;
  background: linear-gradient(45deg, rgba(245,158,11,0.1), rgba(245,158,11,0.05));
  z-index: 1;
}
.gradient-border::before {
  content: '';
  position: absolute;
  inset: 0;
  padding: 1px;
  border-radius: 0.5rem;
  background: linear-gradient(45deg, rgba(245,158,11,0.3), transparent);
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
}
.skills-section {
  display: flex;
  gap: 2rem;
  margin-top: 2rem;
  justify-content: flex-start;
}

.skill-card {
  background: #23272f;
  padding: 1.5rem 2.5rem;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #f59e0b;
  font-weight: 600;
  transition: transform 0.2s, box-shadow 0.2s, background 0.2s;
  cursor: pointer;
  box-shadow: none; /* Remove base glow */
   border: 1px solid rgba(245, 158, 11, 0.1);
}


.skill-card::before {
  content: '';
  position: absolute;
  top: -6px;
  left: -6px;
  right: -6px;
  bottom: -6px;
  border-radius: 1rem;
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.3s;
  box-shadow: 0 0 15px 6px rgba(245, 158, 11, 0.5);
  z-index: -1;
}

.skill-card:hover {
  background: #23272f; /* Keep background same as base */
  color: #f59e0b;
  transform: translateY(-6px) scale(1.03);
  box-shadow: 0 0 20px 4px rgba(245, 158, 11, 0.4); /* Soft outer glow */
}


.skill-card:hover::before {
  opacity: 1;
}
